.input{
    height: 75px;
    border-radius: 50px;
    margin-bottom: 20px;
    display: flex;
    justify-content:flex-end;
    align-items: center;
    font-weight: bold;
    font-size: 40px;
    background-color: rgba(253, 253, 253, 0.05);
    color: white;
    padding: 11px;
    border:  3px solid rgba(240, 231, 231,0.1);
    padding-right: 30px;
    margin: 1px solid #888;
    box-shadow: -3px -3px 8px rgba(27, 3, 3, 0.7), 3px 3px 5px rgba(30, 32, 35, 0.5);
   
}