.clear{
    height: 70px;
    font-size: 1.6em;
    display: flex;
    flex: 0.4;
    background-color: rgba(65, 27, 65, 0.5);;
    margin-top: 0px;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color: white;
    border: 2px solid white;
    cursor: pointer;
    border-radius: 50px;
}
.clear:hover{
    background-color: rgba(5, 5, 5, 0.7);
    transition: 0.2s;
    transform: scale(1.05);

}
.clear:active{
    background-color: rgba(5, 5, 5, 0.7);
    transition: 0.1s;
    transform: scale(1.05);

}