.App{
  background-image: url(./imagenes/fondoCalcu.jpg);
  background-size: 100% 100%;
  background-repeat:repeat-y;
  height: 100vh;
  width: 100%;
  padding-top: 5px;
  font-family: Lato,sans-serif;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.img{
  height: 150px;
  width: 100%;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
img{
  height: 100%;
}
.content{
  width: 300px;
  height: 530px;
  padding: 10px;
  border-radius: 20px;
  box-shadow: 20px 20px 50px rgba(0,0,0,0.5);
  border: 2px solid rgba(255, 255, 255, 0.09);
  background-color: rgba(255, 255, 255, 0.09);
  backdrop-filter: blur(7px);
}
.fila{
  margin: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  
}