.boton-contenedor{
    height: 70px;
    display: flex;
    flex: 1 1;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 38px;
    background-color: rgba(253, 253, 253, 0.05);
    backdrop-filter: blur(7px);
    box-shadow: -3px -3px 8px rgba(27, 3, 3, 0.7), 3px 3px 5px rgb(94, 104,121,0.5);
    color: white;
    border-radius: 50%;
    border: 1px solid rgba(240, 231, 231,0.1);
    margin: 4px ;
    cursor: pointer;
    user-select: none;
   
}
.operador{
    border: 1px solid rgba(251, 243, 251, 0.1);
    background-color: rgba(65, 27, 65, 0.5);
    backdrop-filter: blur(7px);
}
.boton-contenedor:hover{
    background-color: rgba(5, 5, 5, 0.7);
    transition: 0.2s;
    transform: scale(1.1) rotate(22deg);
    border: 1px solid rgba(26, 25, 25, 0.1);
}
.boton-contenedor:active{
    background-color: rgba(5, 5, 5, 0.7);
    transition: 0.1s;
    transform: scale(1.1) rotate(22deg);
    border: 1px solid rgba(26, 25, 25, 0.1);
}
.operador:hover{
    background-color: rgba(5, 5, 5, 0.7);
}